@import url(https://fonts.googleapis.com/css?family=Dosis&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  margin: 0;
  font-family: "Dosis", sans-serif; }

h1 {
  margin: 0; }

.content {
  background-color: #ffffff; }
  .content form {
    background-color: #34495e;
    text-align: center;
    padding: 50px 0; }
    .content form h2 {
      color: #ffffff;
      font-size: 24px;
      margin: 0 auto 20px; }
    .content form input {
      font-family: "Dosis", sans-serif;
      font-size: 16px; }
      .content form input.button {
        background-color: #2980b9;
        border: 0;
        color: #ffffff;
        font-weight: bold;
        outline: none;
        padding: 12px 30px; }
      .content form input.input {
        max-width: 200px;
        padding: 10px 40px; }
  .content .header {
    background-color: #3498db;
    padding: 60px 0 80px;
    text-align: center; }
    .content .header__title {
      color: #fff;
      font-size: 42px;
      margin: 30px 0; }
    .content .header__image img {
      max-width: 130px; }
    .content .header p {
      font-size: 24px;
      color: #fff;
      max-width: 80%;
      margin: 0 auto; }
  .content .result {
    margin-top: 50px; }
    .content .result img {
      display: block;
      margin: 0 auto;
      max-width: 100%; }

